import {ApplyQRcode} from 'api/user'
export default {
	data() {
		return {
			baseUrl: this.$store.state.SRC + "/config/app",
			isAuth: false,
			qrCode: '',
			url:''
		}
	},
	onLoad() {
		ApplyQRcode().then(res=>{
			if(res.result){
				this.qrCode = this.$store.state.SRC+res.QRcode
				this.url = res.url
			}
		})
	},
	methods: {
		
		copy(){
			if(this.url){
				uni.setClipboardData({
				    data: this.url,
				    success: ()=> {
						uni.showToast({
							title:'复制成功',
							icon:"none"
						})
				    }
				});
			}
			
		},

		save() {
			var that = this
			if (this.qrCode&&this.isAuth) {
				uni.getSetting({
					success(res) {

						if (res.authSetting['scope.writePhotosAlbum']) {
							that.isAuth = true

							uni.getImageInfo({
								src: that.qrCode,
								success: function(image) {

									uni.saveImageToPhotosAlbum({
										filePath: image.path,
										success: function() {

											uni.showToast({
												title: '保存成功'
											})
										}

									});
								}
							});

						} else {
							that.isAuth = false

						}
					}
				})
			}
		}
























		

	},

	onShow() {
		var that = this
		uni.getSetting({
			success(res) {
				that.isAuth = res.authSetting['scope.writePhotosAlbum'] ? true : false
				if (!that.isAuth) {
					uni.authorize({
						scope: 'scope.writePhotosAlbum',
						success() {

						}
					})
				}
			}
		})
	},

}
